import Vue from 'vue'
import Router from 'vue-router'
import permit from '../common/permit'
import { MessageBox } from 'element-ui'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'default',
      component: () => import('@/pages/index'),
      children: [
        {
          path: '',
          component: () => import('@/pages/home')
        }
      ]
    },
    {
      path: '/sign',
      name: 'sign',
      component: () => import('@/pages/sign')
    },
    {
      path: '/index',
      name: 'index',
      component: () => import('@/pages/index'),
      children: [
        {
          path: 'home',
          component: () => import('@/pages/home')
        },
        {
          path: 'application',
          component: () => import('@/pages/application')
        },
        {
          path: 'config',
          component: () => import('@/pages/config'),
          beforeEnter (to, from, next) {
            if (permit.getLoginStatus()) {
              next()
            } else {
              MessageBox.alert('请先登陆', '提示', {
                confirmButtonText: '确定',
                callback: action => {
                  if (action === 'confirm') {
                    next('/sign')
                  }
                }
              })
            }
          }
        },
        {
          path: 'boot',
          component: () => import('@/pages/boot')
        },
        {
          path: 'appManage',
          component: () => import('@/pages/appManage'),
          beforeEnter (to, from, next) {
            if (permit.getLoginStatus()) {
              next()
            } else {
              MessageBox.alert('请先登陆', '提示', {
                confirmButtonText: '确定',
                callback: action => {
                  if (action === 'confirm') {
                    next('/sign')
                  }
                }
              })
            }
          }
        },
        {
          path: 'sysManage',
          component: () => import('@/pages/systemManage'),
          beforeEnter (to, from, next) {
            if (permit.getLoginStatus()) {
              next()
            } else {
              MessageBox.alert('请使用管理员权限登陆', '提示', {
                confirmButtonText: '确定',
                callback: action => {
                  if (action === 'confirm') {
                    next('/sign')
                  }
                }
              })
            }
          }},
        {
          path: 'db',
          component: () => import('@/pages/db')
        },
        {
          path: 'register',
          component: () => import('@/pages/register')
        },
        {
          path: 'service-config',
          component: () => import('@/pages/service-config')
        },
        {
          path: 'flow',
          component: () => import('@/pages/flow')
        },
        {
          path: 'top',
          component: () => import('@/pages/top')
        },
        {
          path: 'warn',
          component: () => import('@/pages/warn'),
          beforeEnter (to, from, next) {
            if (permit.getLoginStatus()) {
              next()
            } else {
              MessageBox.alert('请先登陆', '提示', {
                confirmButtonText: '确定',
                callback: action => {
                  if (action === 'confirm') {
                    next('/sign')
                  }
                }
              })
            }
          }
        }, {
          path: 'statusChart',
          component: () => import('@/pages/statusChart')
        }, {
          path: 'user',
          component: () => import('@/pages/user'),
          beforeEnter (to, from, next) {
            if (permit.getLoginStatus()) {
              next()
            } else {
              MessageBox.alert('请先登陆', '提示', {
                confirmButtonText: '确定',
                callback: action => {
                  if (action === 'confirm') {
                    next('/sign')
                  }
                }
              })
            }
          }
        }, {
          path: 'version',
          component: () => import('@/pages/version')
        }, {
          path: 'redis',
          component: () => import('@/pages/redis')
        }, {
          path: 'topology',
          component: () => import('@/pages/topology')
        }, {
          path: 'interface',
          component: () => import('@/pages/interface')
        }, {
          path: 'kafka',
          component: () => import('@/pages/kafka')
        }
      ]
    }
  ]
})



