// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import ElementUI from 'element-ui'
import router from './router'
import 'element-ui/lib/theme-chalk/index.css'
import './common/reset.css'
import './common/common.css'
import VueI18n from 'vue-i18n'
import i18nMessage from './lang'
import axios from 'axios'

Vue.config.productionTip = false
Vue.use(VueI18n)
const lang = window.localStorage.getItem('lang') || 'zh'
const i18n = new VueI18n({
  locale: lang,
  messages: i18nMessage
})

Vue.use(ElementUI, {
  i18n: (key, value) => i18n.t(key, value)
})
window.env = process.env.NODE_ENV
if (process.env.NODE_ENV === 'development') {
  window.localStorage.setItem('loc_host', 'http://pre-watchdog.welike.in/')
  // window.localStorage.setItem('loc_host', 'http://192.168.1.150:8097/')
} else {
  window.localStorage.setItem('loc_host', '/')
}
console.log('process', process.env)

Vue.prototype.axios = axios
axios.defaults.baseURL = window.localStorage.getItem('loc_host')
axios.defaults.withCredentials = true
axios.defaults.crossDomain = true

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  i18n,
  components: {App},
  template: '<App/>'
})



